<template>
  <div id="app">
    <h1>Todo App</h1>
      <task-input-vue @addTodoLists="addTodoLists"></task-input-vue>
      <todo-list-vue @deleteTodo="deleteTodo" :todos="todos"></todo-list-vue>
  </div>
</template>

<script>
import TaskInputVue from "./components/TaskInput.vue";
import TodoListVue from "./components/TodoList.vue"
export default{
  data(){
    return {
      todos:[{todo:"example first",todoDate:"yyyy/mm/dd",address:""}]
    }
  },
  components:{
    TaskInputVue,
    TodoListVue
  },
  methods:{
    addTodoLists(todo){
      console.log(`addTodoList ${todo}`)
      let context = ''
      if(todo){
        if(this.todos.includes(todo)){
          context = ','
          if(confirm(context)){
            this.todos.push(todo)
          }
        }else{
          this.todos.push(todo);
        }
      }else{
        context = ''
        alert(context)
      }
      
    },
    deleteTodo(index){
      console.log(`deleteTodo${index}`)
      if(confirm(`\n ${this.todos[index].todoDate} \n ${this.todos[index].address} \n ${this.todos[index].todo}`))
        this.todos.splice(index,1)
    }
  }
}
</script>

<style lang="scss">
@import "./variables.scss";

#app {
  max-width: 400px;
  margin: 0 auto;
  line-height: 1.4;
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: $vue-green;
  font-size: 20px;
}

h1 {
  text-align: center;
  width: 75%;
}
</style>



